/*--Layout--*/
*, *::before, *::after {
  box-sizing: border-box;
}
.container--full {
  width: 100%;
}
.row, .flex {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}
.column {

&.column--12 {
  width: 100%;
  margin: 0 auto;
  flex: 0 0 100%;
}
&.column--6 {
  width: 50%;
  flex: 0 0 50%;
}

.h-align--center {
  justify-content: center;
}
}
.h-align--left {
  margin-right: auto;
}
.h-align--right {
  margin-left: auto;
}
.v-align--center {
  align-items: center;
}
/*--Padding and Margin Classes--*/
.p-24 {
 padding: 24px;
}
/*--Images--*/
img {
  max-width: 100%;
  height: auto;
}
/*--Text--*/
$base-text-color: #333;
$white: #fff;
$base-font: 'Merriweather', serif;

.body, p, a {
  font-size: 15px;
  font-family: $base-font;
}
.body {
  color: $base-text-color;
}
h1, h2, h3 {
  font-family: $base-font;
}
h4, h5, h6 {
  font-family: $base-font;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.text--white {
  color: $white !important;
}


/*--Nav--*/

nav {
  background-color: #fff;

  li {
    list-style: none;
  }
}

/*--Main--*/
.hero {
  height: 85vh;
}
.hero__text {
  z-index: 1;
  width: 75%;
}

.hero--third {
  height: 33.33%;
  position: relative;
}
.hero--third:after{
  content:"";
  display:block;
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background:rgba(0,0,0,0.4);
}
.hero--third__first {
  background-image: url('./assets/bg1.jpg');
  background-size: cover;
  background-position: center;
}
.hero--third__middle {
  background-image: url('./assets/bg2.jpg');
  background-size: cover;
  background-position: center;
}
.hero--third__last {
  background-image: url('./assets/bg3.jpg');
  background-size: cover;
  background-position: center;
}
/*--Footer--*/
footer {
  height: 124px;
}
